<template>
  <ul class="multiplechoice__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>대표질문</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionInput1"
            v-model="paramObj.representativeQuestion"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>힌트</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionHintInput"
            v-model="paramObj.hint"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>질문</dt>
        <dd class="question__input">
          <textarea
            name=""
            id=""
            class="custom__input"
            ref="questionInput2"
            v-model="paramObj.question"
            :disabled="isPreview"
          ></textarea>
        </dd>
      </dl>
    </li>
    <li class="double__dash">
      <dl>
        <dt>계산식 유형</dt>
        <dd>
          <select
            name=""
            id=""
            ref="selectCalculationFormSelector"
            v-model="paramObj.calculationFormType"
            :disabled="isPreview"
          >
            <option
              :value="formula.type"
              v-for="(formula, index) in calculationFormTypeList"
              :key="index"
            >
              {{ formula.text }}
            </option>
          </select>
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        representativeQuestion: "",
        question: "",
        hint: "",
        calculationFormType: "A",
      },
      calculationFormTypeList: [
        { type: "A", text: "A형 ( 1 + [ ] x 2 = 5 )" },
        { type: "B", text: "B형 ( 1 + 2 x 2 = [ ] )" },
      ],
    };
  },
  props: {
    updateQuestionInfo: [Object, String, Array],
    isPreview: Boolean,
  },
  created() {
    if (this.$env == "development") console.log(this.updateQuestionInfo);
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (
          this.updateQuestionInfo.typicalQuestion !== "" &&
          this.updateQuestionInfo !== ""
        ) {
          // 대표 질문
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo.typicalQuestion;
          // 질문
          this.paramObj.question =
            this.updateQuestionInfo.questions[0].question;
          // 힌트
          this.paramObj.hint = this.updateQuestionInfo.hint;
          //계산식 유형
          this.paramObj.calculationFormType =
            this.updateQuestionInfo.questions[0].choices[0];
        }
      } else {
        if (
          this.updateQuestionInfo[0].typicalQuestion !== "" &&
          this.updateQuestionInfo[0] !== ""
        ) {
          // 대표 질문
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo[0].typicalQuestion;
          // 질문
          this.paramObj.question =
            this.updateQuestionInfo[0].questions[0].question;
          // 힌트
          this.paramObj.hint = this.updateQuestionInfo[0].hint;
          //계산식 유형
          this.paramObj.calculationFormType =
            this.updateQuestionInfo[0].questions[0].choices[0];
        }
      }
    }
  },
  methods: {
    selectCalculationFormType(val) {
      this.selectInputArr = [];
      for (let i = 0; i < val; i++) {
        this.selectInputArr.push({ choice: "" });
      }
    },
    saveBtnClick() {
      const paramObj = {
        representativeQuestion: this.$refs.questionInput1.value,
        hint: this.$refs.questionHintInput.value,
        question: this.$refs.questionInput2.value,
        calculationFormType: this.$refs.selectCalculationFormSelector.value,
      };
      this.$emit("templateInputObj", paramObj);
    },
    initData() {
      this.paramObj = {
        representativeQuestion: "",
        question: "",
        hint: "",
        calculationFormType: "A",
      };
    },
  },
};
</script>
