<template>
  <section class="grid__wrap box__shadow__01 vertical__scroll">
    <h3 class="page__title">
      {{ this.$store.state.pageInfo["치료약 조회"].title }}
    </h3>
    <h5 class="page__sub__title f__regular">
      {{ this.$store.state.pageInfo["치료약 조회"].subTitle }}
    </h5>
    <div class="grid__button__wrap">
      <div class="search__wrap">
        <dl>
          <dt>
            <select
              name=""
              id=""
              ref="subjectSelector"
              @change="changeCategoryBySubject"
              v-model="subjectIdFilter"
            >
              <option value="">전체 진료과목</option>
              <option
                v-for="treatmentSubject in treatmentSubjectList"
                :key="treatmentSubject.index"
                :value="treatmentSubject.codeId"
              >
                {{ treatmentSubject.codeName }}
              </option>
            </select>
            <select
              name=""
              id=""
              ref="categorySelector"
              v-model="categoryIdFilter"
              @change="gridDataFiltering"
            >
              <option value="">전체 카테고리</option>
              <option
                v-for="category in treatmentCategoryList"
                :key="category.index"
                :value="category.codeId"
              >
                {{ category.codeName }}
              </option>
            </select>
            <select
              name=""
              id=""
              ref="typeSelector"
              v-model="typeFilter"
              @change="gridDataFiltering"
            >
              <option value="">전체 타입</option>
              <option
                v-for="type in treatmentTypeList"
                :key="type.index"
                :value="type.codeId"
              >
                {{ type.codeName }}
              </option>
            </select>
            <select
              name=""
              id=""
              ref="levelSelector"
              v-model="levelFilter"
              @change="gridDataFiltering"
            >
              <option value="">전체 레벨</option>
              <option v-for="level in 12" :key="level" :value="level">
                {{ level }}
              </option>
            </select>
            <select
              name=""
              id=""
              ref="stateSelector"
              v-model="useYnFilter"
              @change="gridDataFiltering"
            >
              <option value="">전체 상태</option>
              <option value="Y" selected>사용</option>
              <option value="N">미사용</option>
            </select>
          </dt>
          <dd>
            <input
              class="custom__input"
              type="text"
              ref="treatmentNameInput"
              v-model="medicineNameFilter"
            />
            <!-- @keyup="gridDataFiltering" -->
          </dd>
        </dl>
        <button class="custom__btn" @click="gridDataFiltering">
          <span>
            <img src="@/assets/images/icon/ic-search.svg" alt="검색 아이콘" />
          </span>
          <p>검색</p>
        </button>
      </div>
      <button class="custom__btn" @click="AddMedicineBtnClick">
        <span>
          <img src="@/assets/images/icon/ic-plus.svg" alt="플러스 아이콘" />
        </span>
        <p>치료약 추가</p>
      </button>
    </div>
    <div class="grid__wrap" @click="rowClick($event)">
      <Grid
        class="on"
        ref="gridEl"
        :cols="cols"
        :pagination="true"
        :sort="true"
        :rows="filteredGridData"
      ></Grid>
    </div>
  </section>
</template>
