    gridDataFiltering() {
      this.subjectIdFilter = this.subjectIdFilter.trim();
      this.categoryIdFilter = this.categoryIdFilter.trim();
      this.typeFilter = this.typeFilter.trim();
      this.useYnFilter = this.useYnFilter.trim();
      this.medicineNameFilter = this.medicineNameFilter.trim();
