      this.medicineList = data4;
      this.medicineList.forEach((medicine, index) => {
        medicine.index = index + 1;
      });

      /* const selectedSubject =
        this.$refs.subjectSelector.options[
          this.$refs.subjectSelector.selectedIndex
        ].textContent.trim();
      const selectedCategory =
        this.$refs.categorySelector.options[
          this.$refs.categorySelector.selectedIndex
        ].textContent.trim();
      const selectedType =
        this.$refs.typeSelector.options[
          this.$refs.typeSelector.selectedIndex
        ].textContent.trim();
      const selectedUseYN =
        this.$refs.stateSelector.options[
          this.$refs.stateSelector.selectedIndex
        ].textContent.trim();
      const selectedLevel =
        this.$refs.levelSelector.options[
          this.$refs.levelSelector.selectedIndex
        ].textContent.trim();
      const selectedName = this.$refs.treatmentNameInput.value.trim();
      
      this.filteredGridData = this.filteredGridDataBack;

      if (selectedSubject !== "전체 진료과목") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.subjectName.trim() === selectedSubject
        );
      }

      if (selectedCategory !== "전체 카테고리") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.categoryName.trim() === selectedCategory
        );
      }

      if (selectedType !== "전체 타입") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.typeName.trim() === selectedType
        );
      }

      if (selectedLevel !== "전체 레벨") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.level.trim() === selectedLevel
        );
      }

      if (selectedUseYN !== "전체 상태") {
        let value = "";
        if (selectedUseYN === "사용") {
          value = "Y";
        } else {
          value = "N";
        }
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.useYn.trim() === value
        );
      }
      if (this.filteredGridData.length > 0) {
        const nameFilter = [];
        this.filteredGridData.forEach((obj) => {
          if (obj.name.includes(this.$refs.treatmentNameInput.value)) {
            nameFilter.push(obj);
          }
        });
        this.filteredGridData = nameFilter;
      } */
      this.gridSizeCheck();
    },
    AddMedicineBtnClick() {
      this.$emit("medicinePopupEvent", "Open");
    },
    gridSizeCheck() {
      // No button & No Check box
      if (this.filteredGridData.length > 0) {
        this.$refs.gridEl.$el.classList.add("on");
      } else {
        this.$refs.gridEl.$el.classList.remove("on");
      }
    },
    // 진료과목에 따라 치료약 카테고리 설정
    changeCategoryBySubject() {
      this.categoryIdFilter = "";
      // 진료과목 알콜중독
      if (this.subjectIdFilter == "APP001") {
        this.treatmentCategoryList = this.aStopTreatmentCategoryList;
      } else if (this.subjectIdFilter == "APP002") {
        // 진료과목 인지장애
        this.treatmentCategoryList = this.mStrongTreatmentCategoryList;
      } else {
        this.treatmentCategoryList = [];
      }
      this.gridDataFiltering();
    },
    // deleteSelectedRow() {
    //   for (const idx of this.selectedRemoveRows) {
    //     let deleteMedicine = this.medicineList[idx - 1];
    //     removeMedicine(deleteMedicine.id)
    //       .then((response) => {
    //         if (response.data.status === "OK") {
    //           this.$toast.success("치료약이 삭제되었습니다");
    //           this.$emit("refreshList");
    //         }
    //       })
    //       .catch((error) => this.$toast.error(error.message));
    //   }
    // },
    getUpdateMedicineDetail(id) {
      this.selectedUpdateObj = data4.filter((obj) => obj.id === id);
      this.selectedUpdateObj = this.selectedUpdateObj[0];
      if (this.$env == "development") console.log(this.selectedUpdateObj);
      this.$emit("passUpdateObj", this.selectedUpdateObj);
    },
    getPreviewMedicineDetail(id) {
      this.selectedPreviewObj = data4.filter((obj) => obj.id === id);
      this.selectedPreviewObj = this.selectedPreviewObj[0];
      if (this.$env == "development") console.log(this.selectedPreviewObj);
      this.$emit("passPreviewObj", this.selectedPreviewObj);
