<template>
  <div
    class="popup__wrap"
    :class="{ on: popupEvtFlag === 'Open' }"
    ref="medicineAddPopup"
  >
    <div class="popup__bg"></div>
    <div class="common__popup add__medicine__popup box__shadow__02">
      <div class="popup__top">
        <h4>치료약 {{ popupType }}</h4>
        <button class="pop__close__btn" @click="closePopup"></button>
      </div>
      <div class="popup__main" ref="dropArea" @drop.prevent="onDrop">
        <section class="basic__info__wrap">
          <h5>기본 정보</h5>
          <ul class="vertical__scroll">
            <li>
              <dl>
                <dt>이름</dt>
                <dd>
                  <input
                    type="text"
                    class="custom__input"
                    v-model="reqMedicineObj.name"
                    :disabled="isPreview"
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>진료과목</dt>
                <dd>
                  <select
                    v-model="reqMedicineObj.subjectId"
                    @change="changeCategoryBySubject"
                    :disabled="isPreview"
                  >
                    <option value="">진료과목 선택하세요</option>
                    <option
                      v-for="treatmentSubject in treatmentSubjectList"
                      :key="treatmentSubject.index"
                      :value="treatmentSubject.codeId"
                    >
                      {{ treatmentSubject.codeName }}
                    </option>
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>카테고리</dt>
                <dd>
                  <select
                    v-model="reqMedicineObj.categoryId"
                    :disabled="isPreview"
                  >
                    <option value="">카테고리 선택하세요</option>
                    <option
                      v-for="category in treatmentCategoryList"
                      :key="category.index"
                      :value="category.codeId"
                    >
                      {{ category.codeName }}
                    </option>
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>치료약 타입</dt>
                <dd>
                  <select
                    v-model="reqMedicineObj.typeId"
                    ref="medicineTypeSelector"
                    @change="medicineTypeChange($event.target.value)"
                    :disabled="popupType === '수정' || isPreview"
                  >
                    <option value="">치료약 타입을 선택하세요</option>
                    <option
                      :value="type.codeId"
                      v-for="type in treatmentTypeList"
                      :key="type.index"
                    >
                      {{ type.codeName }}
                    </option>
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>레벨</dt>
                <dd>
                  <select v-model="reqMedicineObj.level" :disabled="isPreview">
                    <option value="">레벨을 선택하세요</option>
                    <option
                      :value="idx + 1"
                      v-for="(level, idx) in 12"
                      :key="idx + 1"
                    >
                      {{ idx + 1 }}
                    </option>
                  </select>
                </dd>
              </dl>
            </li>
            <li class="file__input__list" v-if="addMeaicineType !== '단어회상'">
              <dl>
                <dt>미디어</dt>
                <dd>
                  <!--<div v-if="uploadFileUrl !== ''" @click="openFileLink()">
                    미디어 파일 링크
                  </div>-->
                  <textarea v-if="uploadFile === ''" readonly>
비디오, 이미지, 사운드 등 미디어 파일을 추가해주세요.
                  </textarea>
                  <img
                    src=""
                    ref="imageFreeview"
                    v-show="
                      uploadFile.type === 'image/png' ||
                      uploadFile.type === 'image/gif' ||
                      uploadFile.type === 'image/jpg' ||
                      uploadFile.type === 'image/jpeg'
                    "
                  />
                  <video
                    controls
                    v-show="
                      uploadFile !== '' && uploadFile.type === 'video/mp4'
                    "
                    :type="uploadFile.type"
                    ref="videoFreeview"
                  ></video>
                  <audio
                    v-show="
                      uploadFile.type === 'audio/mpeg' ||
                      uploadFile.type === 'audio/wav' ||
                      uploadFile.type === 'audio/mp3'
                    "
                    :type="uploadFile.type"
                    ref="audioFreeview"
                    controls
                  ></audio>
                </dd>
              </dl>
            </li>
          </ul>
          <div class="button__wrap" v-if="addMeaicineType !== '단어회상'">
            <button
              class="custom__btn delete"
              @click="mediaFileClean"
              v-if="!isPreview"
            >
              <span>
                <img
                  src="@/assets/images/icon/ic-remove.svg"
                  alt="지우기 아이콘"
                />
              </span>
              <p>지우기</p>
            </button>
            <label for="fileInput" v-if="!isPreview">
              <img
                src="@/assets/images/icon/ic-plus.svg"
                alt="미디어 추가 아이콘"
              />
              <p>미디어 추가</p>
            </label>
            <input
              @change="changeFile($event.target)"
              type="file"
              ref="fileInput"
              id="fileInput"
              accept="image/png, image/gif, image/jpg, image/jpeg, auido/mp3, audio/mpeg, audio/wav, video/mp4"
            />
          </div>
        </section>
        <section
          class="medicine__info__wrap"
          v-if="addMeaicineType !== '치료제 타입을 선택하세요'"
        >
          <h5>치료약 정보</h5>
          <SubjectiveTem
            v-if="addMeaicineType === '주관식'"
            ref="subjectiveTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <MultipleChoiceTem
            v-if="addMeaicineType === '객관식'"
            ref="multipleChoiceTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <ADLTem
            v-if="addMeaicineType === 'ADL'"
            ref="adlTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <OXquizTem
            v-if="addMeaicineType === 'OX퀴즈'"
            ref="oxQuizTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <VideoTem
            v-if="
              addMeaicineType === '동영상' ||
              addMeaicineType === '걷기' ||
              addMeaicineType === '스트레칭'
            "
            ref="videoTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :addMeaicineType="addMeaicineType"
            :isPreview="isPreview"
          />
          <WorkSheetTem
            v-if="addMeaicineType === '워크시트'"
            ref="worksheetTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <AudioTem
            v-if="addMeaicineType === '오디오'"
            ref="audioTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <WordRecallTem
            v-if="addMeaicineType === '단어회상'"
            ref="wordRecallTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <CalculationAbilityTem
            v-if="addMeaicineType === '계산력'"
            ref="calculationAbilityTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <SpeedQuizTem
            v-if="addMeaicineType === '스피드OX퀴즈'"
            ref="speedQuizTem"
            @templateInputObj="templateInputObj"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="isPreview"
          />
          <div class="button__wrap" v-if="!isPreview">
            <button class="custom__btn" @click="saveBtnClick">
              <span>
                <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
              </span>
              <p>저장</p>
            </button>
          </div>
        </section>
      </div>
      <LoadingBar v-if="isLoading" />
    </div>
  </div>
</template>
<script>
import SubjectiveTem from "@/components/MedicineInquiry/MedicineAddPopup/SubjectiveTem";
import MultipleChoiceTem from "@/components/MedicineInquiry/MedicineAddPopup/MultipleChoiceTem";
import ADLTem from "@/components/MedicineInquiry/MedicineAddPopup/ADLTem";
import OXquizTem from "@/components/MedicineInquiry/MedicineAddPopup/OXquizTem";
import VideoTem from "@/components/MedicineInquiry/MedicineAddPopup/VideoTem";
import AudioTem from "@/components/MedicineInquiry/MedicineAddPopup/AudioTem";
import WorkSheetTem from "@/components/MedicineInquiry/MedicineAddPopup/WorkSheetTem";
import WordRecallTem from "@/components/MedicineInquiry/MedicineAddPopup/WordRecallTem";
import CalculationAbilityTem from "@/components/MedicineInquiry/MedicineAddPopup/CalculationAbilityTem";
import SpeedQuizTem from "@/components/MedicineInquiry/MedicineAddPopup/SpeedQuizTem";
// import { addMedicine, updateMedicine } from "@/api/medicine";
import { upload } from "@/utill/getData";
import { clear } from "@/utill/commom";
import { isNotEmpty } from "@/utill/validator";
import LoadingBar from "@/components/common/LoadingBar";

export default {
  components: {
    SubjectiveTem,
    MultipleChoiceTem,
    ADLTem,
    OXquizTem,
    VideoTem,
    WorkSheetTem,
    AudioTem,
    WordRecallTem,
    CalculationAbilityTem,
    SpeedQuizTem,
    LoadingBar,
  },
  props: {
    popupEvtFlag: String,
    treatmentSubjectList: Array,
    aStopTreatmentCategoryList: Array,
    mStrongTreatmentCategoryList: Array,
    treatmentTypeList: Array,
    updateMedicine: Object,
    previewMedicine: Object,
  },
  data() {
    return {
      uploadFile: "",
      addMeaicineType: "치료제 타입을 선택하세요",
      freeView: "",
      reqMedicineObj: {
        subjectId: "",
        categoryId: "",
        level: "1",
        name: "",
        typeId: "",
        useYn: "Y",
        questionInfo: [],
      },
      uploadFileUrl: "",
      popupType: "추가",
      updateQuestionInfo: "",
      treatmentCategoryList: [],
      isLoading: false,
      isPreview: false,
    };
  },
  watch: {
    updateMedicine() {
      if (this.updateMedicine != null || this.updateMedicine !== "") {
        this.reqMedicineObj.subjectId = this.updateMedicine.subjectId;
        this.changeCategoryBySubject();
        this.reqMedicineObj.categoryId = this.updateMedicine.category;
        this.reqMedicineObj.id = this.updateMedicine.id;
        this.reqMedicineObj.level = this.updateMedicine.level;
        this.reqMedicineObj.name = this.updateMedicine.name;
        this.reqMedicineObj.typeId = this.updateMedicine.type;
        this.reqMedicineObj.useYn = this.updateMedicine.useYn;
        this.updateQuestionInfo = Object.assign(
          {},
          this.updateMedicine.questionInfo[0]
        );
        this.uploadFileUrl = this.updateQuestionInfo.url;
        if (this.uploadFileUrl != "") {
          this.checkFormatByUrl(this.uploadFileUrl);
        }
        if (this.$env == "development") console.log(this.updateQuestionInfo);
        this.medicineTypeChange(this.reqMedicineObj.typeId);
        this.popupType = "수정";
      } else {
        this.popupType = "추가";
        this.medicineTypeChange();
      }
    },
    previewMedicine() {
      if (this.previewMedicine != null || this.previewMedicine !== "") {
        this.reqMedicineObj.subjectId = this.previewMedicine.subjectId;
        this.changeCategoryBySubject();
        this.reqMedicineObj.categoryId = this.previewMedicine.category;
        this.reqMedicineObj.id = this.previewMedicine.id;
        this.reqMedicineObj.level = this.previewMedicine.level;
        this.reqMedicineObj.name = this.previewMedicine.name;
        this.reqMedicineObj.typeId = this.previewMedicine.type;
        this.reqMedicineObj.useYn = this.previewMedicine.useYn;
        this.updateQuestionInfo = Object.assign(
          {},
          this.previewMedicine.questionInfo[0]
        );
        this.uploadFileUrl = this.updateQuestionInfo.url;
        if (this.uploadFileUrl != "") {
          this.checkFormatByUrl(this.uploadFileUrl);
        }
        if (this.$env == "development") console.log(this.updateQuestionInfo);
        this.medicineTypeChange(this.reqMedicineObj.typeId);
        this.popupType = "미리보기";
        this.isPreview = true;
      }
    },
  },
  computed: {
    medicineManagement() {
      return this.$store.state.treatmentManagement;
    },
  },
  mounted() {
    this.bindDragAndDropEvent();
  },
  methods: {
    bindDragAndDropEvent() {
      const events = ["dragenter", "dragover", "dragleave", "drop"];
      events.forEach((event) => {
        this.$refs.dropArea.addEventListener(event, (e) => {
          e.preventDefault();
        });
      });
    },
    onDrop(e) {
      this.changeFile(e.dataTransfer);
    },
    closePopup() {
      this.$emit("closePopup", "Close");
      this.popupType = "추가";
      this.mediaFileClean();
      clear(this.reqMedicineObj);
      clear(this.updateMedicine);
      clear(this.previewMedicine);
      clear(this.updateQuestionInfo);
      this.addMeaicineType = "치료제 타입을 선택하세요";
      this.uploadFileUrl = "";
      this.treatmentCategoryList = [];
      this.isPreview = false;
    },
    // 치료약 연속 추가 시 팝업 초기화
    clearTitleAndMedia() {
      this.mediaFileClean();
      this.reqMedicineObj.name = "";
      this.reqMedicineObj.questionInfo = [];
      clear(this.updateQuestionInfo);
      this.uploadFileUrl = "";
      switch (this.addMeaicineType) {
        case "오디오":
          this.$refs.audioTem.initData();
          break;
        case "주관식":
          this.$refs.subjectiveTem.initData();
          break;
        case "객관식":
          this.$refs.multipleChoiceTem.initData();
          break;
        case "ADL":
          this.$refs.adlTem.initData();
          break;
        case "OX퀴즈":
          this.$refs.oxQuizTem.initData();
          break;
        case "워크시트":
          this.$refs.worksheetTem.initData();
          break;
        case "단어회상":
          this.$refs.wordRecallTem.initData();
          break;
        case "스피드OX퀴즈":
          this.$refs.speedQuizTem.initData();
          break;
        case "계산력":
          this.$refs.calculationAbilityTem.initData();
          break;
        default:
          // 동영상, 걷기, 스트레칭
          this.$refs.videoTem.initData();
          break;
      }
    },
    changeFile(param) {
      if (this.$env == "development") console.log(param);
      if (param.files.length > 0) {
        this.mediaFileClean();
        this.uploadFile = param.files[0];
        if (this.uploadFile.size > 1024 * 1024 * 2000) {
          this.$toast.error("미디어 파일이 2GB 이상 시 추가 하실수 없습니다.");
          this.mediaFileClean();
          return;
        }
        const reader = new FileReader();
        const videoOrAudioUrl = URL.createObjectURL(this.uploadFile);
        const audioFreeViewEl = this.$refs.audioFreeview;
        const videoFreeViewEl = this.$refs.videoFreeview;
        const imgFreeViewEl = this.$refs.imageFreeview;

        reader.readAsDataURL(this.uploadFile);
        if (
          this.uploadFile.type === "image/gif" ||
          this.uploadFile.type === "image/png" ||
          this.uploadFile.type === "image/jpg" ||
          this.uploadFile.type === "image/jpeg"
        ) {
          reader.onload = function () {
            imgFreeViewEl.src = this.result;
          };
        } else if (this.uploadFile.type === "video/mp4") {
          videoFreeViewEl.src = videoOrAudioUrl;
          //this.uploadFileToServer(this.uploadFile);
        } else if (
          this.uploadFile.type === "audio/mpeg" ||
          this.uploadFile.type === "audio/wav" ||
          this.uploadFile.type === "audio/mp3"
        ) {
          audioFreeViewEl.src = videoOrAudioUrl;
        }
      } else {
        this.uploadFile = "";
      }
    },
    medicineTypeChange(val) {
      let vm = this;
      if (val) {
        vm.treatmentTypeList.forEach(function (type) {
          if (type.codeId === val) {
            vm.addMeaicineType = type.codeName;
          }
        });
      } else {
        vm.addMeaicineType = "치료제 타입을 선택하세요";
      }
    },
    mediaFileClean() {
      if (this.uploadFile) {
        this.uploadFile = "";
        this.$refs.imageFreeview.src = "";
        this.$refs.videoFreeview.src = "";
        this.$refs.audioFreeview.src = "";
      }
    },
    templateInputObj(param) {
      // 저장 버튼 클릭시 : 치료약 템플릿 별 인풋 오브젝트
      if (this.$env == "development") console.log(param);
      if (
        this.addMeaicineType === "OX퀴즈" ||
        this.addMeaicineType === "동영상" ||
        this.addMeaicineType === "오디오" ||
        this.addMeaicineType === "단어회상"
      ) {
        this.reqMedicineObj.questionInfo.push({
          typicalQuestion: param.representativeQuestion || "",
          hint: param.hint || "",
          questions: [
            {
              question: param.question || "",
              answer: param.answer || "",
            },
          ],
          url: this.uploadFileUrl,
        });
      } else if (
        this.addMeaicineType === "주관식" ||
        this.addMeaicineType === "워크시트"
      ) {
        this.reqMedicineObj.questionInfo.push({
          typicalQuestion: param.representativeQuestion,
          hint: param.hint || "",
          questions: param.questionArr,
          url: this.uploadFileUrl,
        });
      } else if (this.addMeaicineType === "객관식") {
        let choicesArr = [];
        let answerStr = "";
        param.selectInputArr.forEach((obj) => {
          choicesArr.push(obj.choice);
        });

        if (param.answerSizeArr.length == 1) {
          answerStr = param.answerSizeArr[0].answerNumber;
        } else {
          const tempAnswerArr = [];
          param.answerSizeArr.forEach((obj) => {
            tempAnswerArr.push(obj.answerNumber);
          });
          answerStr = tempAnswerArr.join(",");
        }
        this.reqMedicineObj.questionInfo.push({
          typicalQuestion: param.representativeQuestion,
          hint: param.hint || "",
          questions: [
            {
              question: param.question,
              answer: answerStr,
              choices: choicesArr,
            },
          ],
          url: this.uploadFileUrl,
        });
      } else if (this.addMeaicineType === "ADL") {
        this.reqMedicineObj.questionInfo.push({
          typicalQuestion: "",
          questions: [{ question: "", answer: "" }],
          url: param.linkInput,
        });
      } else if (this.addMeaicineType === "계산력") {
        let choicesArr = [];
        choicesArr.push(param.calculationFormType);
        this.reqMedicineObj.questionInfo.push({
          typicalQuestion: param.representativeQuestion,
          hint: param.hint || "",
          questions: [
            {
              question: param.question,
              choices: choicesArr,
            },
          ],
          url: this.uploadFileUrl,
        });
      } else if (this.addMeaicineType === "스피드OX퀴즈") {
        let questionArray = [];
        for (let i = 0; i < param.questionArr.length; i++) {
          let tempChoice = [];
          tempChoice.push(param.questionArr[i].choiceO);
          tempChoice.push(param.questionArr[i].choiceX);
          let tempObj = {
            question: param.questionArr[i].question,
            answer: param.questionArr[i].answer,
            timeLimit: param.questionArr[i].timeLimit,
            choices: tempChoice,
          };
          questionArray.push(tempObj);
        }

        this.reqMedicineObj.questionInfo.push({
          typicalQuestion: param.representativeQuestion,
          hint: param.hint || "",
          questions: questionArray,
          url: this.uploadFileUrl,
        });
      } else {
        // 오디오 등 기타 질문 없는 치료약 타입 시
        this.reqMedicineObj.questionInfo.push({
          typicalQuestion: "",
          hint: param.hint || "",
          questions: [{ question: "", answer: "" }],
          url: this.uploadFileUrl,
        });
      }
      this.reqMedicineObj.category = this.reqMedicineObj.categoryId;
      this.reqMedicineObj.type = this.reqMedicineObj.typeId;

      if (this.popupType === "추가") {
        // addMedicine(this.reqMedicineObj)
        //   .then((response) => {
        //     if (this.$env == "development") console.log(response);
        //     if (response.data.status === "OK") {
        //       this.isLoading = false;
        //       this.$toast.success("치료약 추가가 완료되었습니다");
        //       //this.closePopup();
        //       this.clearTitleAndMedia();
        //       this.$emit("refreshList");
        //     }
        //   })
        //   .catch((error) => {
        //     this.isLoading = false;
        //     this.$toast.error(error.message);
        //   });
      } else if (this.popupType === "수정") {
        // updateMedicine(this.reqMedicineObj, false)
        //   .then((response) => {
        //     if (this.$env == "development") console.log(response);
        //     if (
        //       response.data.status === "OK" ||
        //       response.data.code === "00000"
        //     ) {
        //       // 신규 미디어 파일 추가 시 기존 파일 삭제
        //       if (
        //         this.uploadFileUrl !== this.updateQuestionInfo.url &&
        //         this.updateQuestionInfo.url !== ""
        //       ) {
        //         const urlObj = new URL(this.updateQuestionInfo.url);
        //         if (this.$env == "development")
        //           console.log(`기존 미디어 파일 삭제: ${urlObj.pathname}`);
        //         deleteFile(urlObj.pathname.substring(1));
        //       }
        //       this.isLoading = false;
        //       this.$toast.success("치료약 수정이 완료되었습니다");
        //       this.closePopup();
        //       this.$emit("refreshList");
        //     } else if (response.data.code === "403") {
        //       this.isLoading = false;
        //       this.$toast.error(response.data.message);
        //     } else if (response.data.code === "202") {
        //       this.forceUpdateMedicine(this.reqMedicineObj);
        //     }
        //   })
        //   .catch((error) => {
        //     this.isLoading = false;
        //     this.$toast.error(error.message);
        //   });
      }
      // // 저장 버튼 클릭시 : 치료약 템플릿 별 인풋 오브젝트
    },
    forceUpdateMedicine() {
      this.$confirm({
        // showCancelButton: true,
        title: "",
        message: "이미 다른 치료제에 사용중입니다. 그래도 수정하시겠습니까?",
        button: {
          no: "아니오",
          yes: "네",
        },
        callback: (confirm) => {
          if (confirm) {
            // updateMedicine(medicine, true)
            //   .then((response) => {
            //     if (
            //       response.data.status === "OK" ||
