            //       if (
            //         this.uploadFileUrl !== this.updateQuestionInfo.url &&
            //         this.updateQuestionInfo.url !== ""
            //       ) {
            //         const urlObj = new URL(this.updateQuestionInfo.url);
            //         if (this.$env == "development")
            //           console.log(`기존 미디어 파일 삭제: ${urlObj.pathname}`);
            //         deleteFile(urlObj.pathname.substring(1));
            //       }
            //       this.isLoading = false;
            //       this.$toast.success("치료약 수정이 완료되었습니다");
            //       this.closePopup();
            //       this.$emit("refreshList");
            //     }
            //   })
            //   .catch((error) => this.$toast.error(error.message));
          }
        },
      });
    },
    saveBtnClick() {
      this.uploadFileToServerAndSaveMedicine(this.uploadFile);
      /*switch (this.addMeaicineType) {
        case "오디오":
          this.uploadFileToServerAndSaveMedicine(this.uploadFile);
          break;
        case "주관식":
          this.$refs.subjectiveTem.saveBtnClick();
          break;
        case "객관식":
          this.$refs.multipleChoiceTem.saveBtnClick();
          break;
        case "ADL":
          this.$refs.adlTem.saveBtnClick();
          break;
        case "OX퀴즈":
          this.$refs.oxQuizTem.saveBtnClick();
          break;
        case "동영상":
          //this.$refs.videoTem.saveBtnClick();
          this.uploadFileToServerAndSaveMedicine(this.uploadFile);
          break;
        case "워크시트":
          this.$refs.worksheetTem.saveBtnClick();
          break;
        default:
          this.uploadFileToServerAndSaveMedicine(this.uploadFile);
      }*/
    },
    validateMedicine() {
      if (!isNotEmpty(this.reqMedicineObj.name)) {
        this.$toast.error("치료약 이름을 입력하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqMedicineObj.subjectId)) {
        this.$toast.error("진료과목을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqMedicineObj.categoryId)) {
        this.$toast.error("카테고리를 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqMedicineObj.typeId)) {
        this.$toast.error("치료약 타입을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqMedicineObj.level)) {
        this.$toast.error("치료약 레벨을 선택하세요.");
        return false;
      }
      return true;
    },
    parseUploadFile(file) {
      //let suffix = file.name.split(".")[1];
      let index = file.name.lastIndexOf(".");
      let suffix = file.name.substr(index + 1);
      let key = new Date().getTime() + "." + suffix;
      let params = {
        Key: key,
        ContentType: file.type,
        Body: file,
      };
      return params;
    },
    uploadFileToServerAndSaveMedicine(file) {
      if (this.$env == "development") console.log(file.name);
      if (!this.validateMedicine()) return;
      let vm = this;
      // 신규 치료약 추가 시 OR 치료약 수정 시 미디어 파일 있는 경우
      // 파일 업로드 후 url 받고 치료약 저장
      this.isLoading = true;
      if (file.name) {
        const params = vm.parseUploadFile(file);
        upload(
          params,
          function (err, data) {
            if (err) {
              if (this.$env == "development")
                console.log("upload error", err.code, err.message);
            } else {
              // finsh uploading and close popup
              if (this.$env == "development") console.log("upload success");
              //if (this.$env == "development") console.log(data);
              let url = data.Location; // link
              if (this.$env == "development") console.log(url);
              // 미디어 파일 업로드 후 URL 저장
              vm.uploadFileUrl = url;
              vm.saveMedicineByType();
            }
          },
          function (e) {
            let precent = Math.trunc(
              (parseInt(e.loaded, 10) / parseInt(e.total, 10)) * 100
            );
            if (this.$env == "development")
              console.log(`upload video file ${file.name} - ${precent}%`);
          }
        );
      } else {
        // 신규 치료약 추가 시 OR 치료약 수정 시 미디어 파일 없는 경우 OR 기존 미디어 파일 변경 없는 경우
        vm.saveMedicineByType();
      }
    },
    openFileLink() {
      window.open(
        this.uploadFileUrl,
        "newwindow",
        "height=200,width=400,top=0,left=0,toolbar=no,menubar=no,scrollbars=no, resizable=no,location=no, status=no"
      );
    },
    checkFormatByUrl(url) {
      let suffix = "";
      this.uploadFile = {};
      let urlArray = url.split(".");
      if (urlArray.length > 0) {
        suffix = urlArray[urlArray.length - 1];
      }
      if (
        suffix === "gif" ||
        suffix === "png" ||
        suffix === "jpg" ||
        suffix === "jpeg"
      ) {
        this.uploadFile.type = `image/${suffix}`;
        this.$refs.imageFreeview.src = url;
      } else if (suffix === "mpeg" || suffix === "wav" || suffix === "mp3") {
        this.uploadFile.type = `audio/${suffix}`;
        this.$refs.audioFreeview.src = url;
      } else if (suffix === "mp4") {
        this.uploadFile.type = `video/${suffix}`;
        this.$refs.videoFreeview.src = url;
      } else {
        this.uploadFile.type = "";
      }
      if (this.$env == "development") console.log(this.uploadFile);
    },
    saveMedicineByType() {
      switch (this.addMeaicineType) {
        case "주관식":
          this.$refs.subjectiveTem.saveBtnClick();
          break;
        case "객관식":
          this.$refs.multipleChoiceTem.saveBtnClick();
          break;
        case "ADL":
          this.$refs.adlTem.saveBtnClick();
          break;
        case "OX퀴즈":
          this.$refs.oxQuizTem.saveBtnClick();
          break;
        case "동영상":
          this.$refs.videoTem.saveBtnClick();
          break;
        case "걷기":
          this.$refs.videoTem.saveBtnClick();
          break;
        case "스트레칭":
          this.$refs.videoTem.saveBtnClick();
          break;
        case "오디오":
          this.$refs.audioTem.saveBtnClick();
          break;
        case "워크시트":
          this.$refs.worksheetTem.saveBtnClick();
          break;
        case "단어회상":
          this.$refs.wordRecallTem.saveBtnClick();
          break;
        case "스피드OX퀴즈":
          this.$refs.speedQuizTem.saveBtnClick();
          break;
        case "계산력":
          this.$refs.calculationAbilityTem.saveBtnClick();
          break;
        default:
          this.templateInputObj();
      }
    },
    // 진료과목에 따라 치료약 카테고리 설정
    changeCategoryBySubject() {
      this.reqMedicineObj.categoryId = "";
      // 진료과목 알콜중독
      if (this.reqMedicineObj.subjectId == "APP001") {
        this.treatmentCategoryList = this.aStopTreatmentCategoryList;
      } else if (this.reqMedicineObj.subjectId == "APP002") {
        // 진료과목 인지장애
        this.treatmentCategoryList = this.mStrongTreatmentCategoryList;
      } else {
        this.treatmentCategoryList = [];
      }
    },
  },
};
</script>
