<template>
  <ul class="subjective__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>대표질문</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionInput1"
            v-model="paramObj.representativeQuestion"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>힌트</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionHintInput"
            v-model="paramObj.hint"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>질문수</dt>
        <dd>
          <select
            name=""
            id=""
            ref="answerSizeSelctor"
            @change="answerSizeChange($event.target.value)"
            v-model="paramObj.answerCnt"
            :disabled="isPreview"
          >
            <option
              :value="questionSize"
              v-for="questionSize in totalQuestionLimit"
              :key="questionSize"
            >
              {{ questionSize }}
            </option>
          </select>
        </dd>
      </dl>
    </li>
    <li
      class="question__list double__dash"
      v-for="(questionInfo, idx) in answerInputArr"
      :key="idx"
    >
      <dl>
        <dt>질문 {{ idx + 1 }}</dt>
        <dd class="question__input">
          <textarea
            name=""
            id=""
            :value="questionInfo.question"
            @keyup="changeQuestion($event.target.value, idx)"
            class="custom__input"
            :disabled="isPreview"
          ></textarea>
        </dd>
      </dl>
      <dl>
        <dt>제한시간 {{ idx + 1 }}</dt>
        <dd>
          <input
            name=""
            id=""
            :value="questionInfo.timeLimit"
            @keyup="changeTimeLimit($event.target.value, idx)"
            class="custom__input"
            :disabled="isPreview"
            type="number"
          />
        </dd>
      </dl>
      <dl>
        <dt>O 값 {{ idx + 1 }}</dt>
        <dd>
          <input
            name=""
            id=""
            :value="questionInfo.choiceO"
            @keyup="changeChoiceO($event.target.value, idx)"
            class="custom__input"
            :disabled="isPreview"
          />
        </dd>
      </dl>
      <dl>
        <dt>X 값 {{ idx + 1 }}</dt>
        <dd>
          <input
            name=""
            id=""
            :value="questionInfo.choiceX"
            @keyup="changeChoiceX($event.target.value, idx)"
            class="custom__input"
            :disabled="isPreview"
          />
        </dd>
      </dl>
      <dl>
        <dt>정답 {{ idx + 1 }}</dt>
        <dd>
          <select
            name=""
            id=""
            @change="changeOX($event.target.value, idx)"
            v-model="questionInfo.answer"
            :disabled="isPreview"
          >
            <option value="O">O</option>
            <option value="X">X</option>
          </select>
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        representativeQuestion: "",
        answerCnt: 1,
        hint: "",
      },
      answerInputArr: [
        { question: "", timeLimit: "", choiceO: "", choiceX: "", answer: "O" },
      ],
      //answerInputArr: [],
      totalQuestionLimit: 6,
    };
  },
  props: {
    updateQuestionInfo: [String, Object, Array],
    isPreview: Boolean,
  },
  created() {
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (
          this.updateQuestionInfo.typicalQuestion !== "" &&
          this.updateQuestionInfo !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo.typicalQuestion;
          this.paramObj.answerCnt = this.updateQuestionInfo.questions.length;
          this.answerInputArr = [];
          for (let i = 0; i < this.paramObj.answerCnt; i++) {
            this.answerInputArr.push({
              question: this.updateQuestionInfo.questions[i].question,
              answer: this.updateQuestionInfo.questions[i].answer,
              timeLimit: this.updateQuestionInfo.questions[i].timeLimit,
              choiceO: this.updateQuestionInfo.questions[i].choices[0],
              choiceX: this.updateQuestionInfo.questions[i].choices[1],
            });
          }
          this.paramObj.hint = this.updateQuestionInfo.hint;
        }
      } else {
        if (
          this.updateQuestionInfo[0].typicalQuestion !== "" &&
          this.updateQuestionInfo[0] !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo[0].typicalQuestion;
          this.paramObj.answerCnt = this.updateQuestionInfo[0].questions.length;
          this.answerInputArr = [];
          for (let i = 0; i < this.paramObj.answerCnt; i++) {
            this.answerInputArr.push({
              question: this.updateQuestionInfo[0].questions[i].question,
              answer: this.updateQuestionInfo[0].questions[i].answer,
              timeLimit: this.updateQuestionInfo.questions[i].timeLimit,
              choiceO: this.updateQuestionInfo.questions[i].choices[0],
              choiceX: this.updateQuestionInfo.questions[i].choices[1],
            });
          }
          this.paramObj.hint = this.updateQuestionInfo[0].hint;
        }
      }
    } else {
      this.answerSizeChange(1);
    }
  },
  methods: {
    answerSizeChange(val) {
      this.answerInputArr = [];

      for (let i = 0; i < val; i++) {
        this.answerInputArr.push({
          question: "",
          timeLimit: "",
          choiceO: "",
          choiceX: "",
          answer: "O",
        });
      }
    },
    changeQuestion(val, idx) {
      this.answerInputArr[idx].question = val;
    },
    changeTimeLimit(val, idx) {
      this.answerInputArr[idx].timeLimit = val;
    },
    changeChoiceO(val, idx) {
      this.answerInputArr[idx].choiceO = val;
    },
    changeChoiceX(val, idx) {
      this.answerInputArr[idx].choiceX = val;
    },
    changeOX(val, idx) {
      this.answerInputArr[idx].answer = val;
    },
    saveBtnClick() {
      const paramObj = {
        representativeQuestion: this.$refs.questionInput1.value,
        questionArr: this.answerInputArr,
        hint: this.$refs.questionHintInput.value,
      };
      this.$emit("templateInputObj", paramObj);
    },
    initData() {
      this.paramObj = {
        representativeQuestion: "",
        answerCnt: 1,
        hint: "",
      };
      this.answerInputArr = [
        { question: "", timeLimit: "", choiceO: "", choiceX: "", answer: "O" },
      ];
    },
  },
};
</script>
