<template>
  <main class="medicine__management__main">
    <MedicineAddPopup
      :popupEvtFlag="popupEvtFlag"
      @closePopup="closeAddMedicinePopup"
      :treatmentSubjectList="treatmentSubjectList"
      :aStopTreatmentCategoryList="aStopTreatmentCategoryList"
      :mStrongTreatmentCategoryList="mStrongTreatmentCategoryList"
      :treatmentTypeList="treatmentTypeList"
      @refreshList="getMedicines"
      :updateMedicine="updateMedicine"
      :previewMedicine="previewMedicine"
    />
    <MedicineGrid
      :medicineList="getMedicineList"
      @medicinePopupEvent="medicinePopupEvent"
      :treatmentSubjectList="treatmentSubjectList"
      :aStopTreatmentCategoryList="aStopTreatmentCategoryList"
      :mStrongTreatmentCategoryList="mStrongTreatmentCategoryList"
      :treatmentTypeList="treatmentTypeList"
      @refreshList="getMedicines()"
      @passUpdateObj="passUpdateObj"
      @passPreviewObj="passPreviewObj"
    />
  </main>
</template>
<script>
import "@/assets/css/addTreatment.css";
import "@/assets/css/addMedicinePopup.css";
import "@/assets/css/medicineManagement.css";
import MedicineGrid from "@/components/MedicineInquiry/MedicineGrid";
import MedicineAddPopup from "@/components/MedicineInquiry/MedicineAddPopup";
import data4 from "@/data/remedyManage/data4.json";
import data1 from "@/data/medicineManage/data1.json";
import data2 from "@/data/medicineManage/data2.json";
import data3 from "@/data/medicineManage/data3.json";
import data44 from "@/data/medicineManage/data4.json";

export default {
  components: {
    MedicineGrid,
    MedicineAddPopup,
  },
  data() {
    return {
      popupEvtFlag: "Close",
      medicineList: [],
      treatmentSubjectList: [],
      aStopTreatmentCategoryList: [],
      mStrongTreatmentCategoryList: [],
      treatmentTypeList: [],
      updateMedicine: {},
      previewMedicine: {},
    };
  },
  computed: {
    treatmentManagement() {
      return this.$store.state.treatmentManagement;
    },
    getMedicineList() {
      return this.medicineList;
    },
  },
  created() {
    this.getMedicines();
    this.getTreatmentSubjectList();
    this.getAStopTreatmentCategoryList();
    this.getMStrongTreatmentCategoryList();
    this.getTreatmentTypeList();
  },
  methods: {
    closeAddMedicinePopup(param) {
      this.popupEvtFlag = param;
    },
    medicinePopupEvent(param) {
      this.popupEvtFlag = param;
    },
    getMedicines() {
      this.medicineList = data4;
      this.medicineList.forEach((medicine, index) => {
        medicine.index = index + 1;
      });
    },
    getTreatmentSubjectList() {
      this.treatmentSubjectList = data1.codeList;
    },
    // A-Stop(알콜중독)치료약 카테고리 조회
    getAStopTreatmentCategoryList() {
      this.aStopTreatmentCategoryList = data2.codeList;
    },
    // M-Strong(인지장애)치료약 카테고리 조회
    getMStrongTreatmentCategoryList() {
      this.mStrongTreatmentCategoryList = data3.codeList;
    },
    getTreatmentTypeList() {
      this.treatmentTypeList = data44.codeList;
    },
    passUpdateObj(obj) {
      this.updateMedicine = obj;
      this.medicinePopupEvent("Open");
    },
    passPreviewObj(obj) {
      this.previewMedicine = obj;
      this.medicinePopupEvent("Open");
    },
  },
};
</script>
